import Header from "../components/header";

export default ()=>{
    return(
        <div>
            <Header/>
            <div class="container">
            <form action="/login" method="POST">
                <input name="username"/>
                <input name="password"/>
                <a href="/register">Sign-Up</a>
                <button>Submit</button>
            </form>
            </div>
        </div>
    )
}